.socialmap {
   margin: 0 auto;
    /* display: flex;
    justify-content: center; */
    height: 300px;
    max-width: 1000px;
    width: 100%;
    padding-top: 2rem;
    margin-top: 2rem;
    margin-bottom: 4rem;
}
#map {
    display: flex;
    justify-content: center;
    margin: 0 auto;
    max-width: 1080px;
    min-height: 300px;
    -webkit-filter: grayscale(100%);
    /* Safari 6.0 - 9.0 */
    filter: grayscale(100%);
    margin-bottom: 0rem;
}

.contacts {



		&__form {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      margin-top: 40px;
      margin-bottom: 40px;
		}

		// .contact__input

		&__input {

        max-width: 1000px;
  width: 100%;
height: 60px;
border: none;
border-bottom: 2px solid var(--color-tundora);
margin-bottom: 10px;
		}

		// .contact__submit

		&__submit {

        max-width: 1000px;
  width: 100%;
height: 60px;
// font-family: 'Quicksand';
font-style: normal;
font-weight: 500;
font-size: 20px;
line-height: 25px;
border: none;
color: #FFFFFF;
background-color: var(--color-dark);
transition: all 0.3s;
      cursor: pointer;

     &:hover{
      color: var(--color-dark);
      background-color: #fff;
      border: 2px solid var(--color-tundora);
      transition: all 0.3s;
      cursor: pointer;
     }
		}
}
